.appBody {
  font-family: Georgia, "Times New Roman", Times, serif;
  color: rgb(252, 252, 252);
  background-color: black;
  /*background-image: url('src/ron.jpg');
   */
}

.top {
  background-color: grey;
  height: 7vh;
  margin-top: -16px;
  padding-top: 20px;
}
.topTitle {
  font-size: 20px;
  padding-left: 40px;
}
span {
  float: right;
  font-style: italic;
  font-size: 12px;
  padding-right: 30px;
}

.App {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.accordianDiv {
  height: 60vh;
  width: 80vw;
}
.quote {
  height: 10vh;
  width: 80vw;
  background-color: rgb(83, 83, 83);
  text-align: center;
  display: none;
  padding-top: 20px;
}
.display {
  display: block;
}
.button {
  height: 5vh;
  width: 80vw;
  background-color: gray;
  outline: none;
}
